import DefaultInputItem from 'component/element/element_basic/DefaultInputItem/DefaultInputItem'
import Button from 'component/element/element_basic/Button/Button'
import styles from './SignUpPage.module.scss'
import { ReactComponent as Logo } from 'assets/icons/logo.svg'
import { useState } from 'react'
import { register } from 'api/auth'

const SignUpPage = () => {
  const [account, setAccount] = useState('')
  const [name, setName] = useState('')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [checkPassword, setCheckPassword] = useState('')

  const handleClick = async () => {
    if (account.length === 0 || name.length === 0 || email.length === 0 || password.length === 0 || checkPassword.length === 0) {
      return
    }
    if (account.length > 10 || name.length > 50 || email.length > 10 || password.length > 10 || checkPassword.length > 10) {
      return
    }
    const { success } = await register({ account, name, email, password, checkPassword })
    if (success) {
      console.log('')
    } else {
      console.log('')
    }
  }

  return (
        <div className={styles.registerContainer}>
          <div className={styles.logoContainer}>
            <Logo />
          </div>
          <div className={styles.title}>
            <h3 className='Bold'>
             
            </h3>
          </div>
          <div className={styles.inputContainer}>
            <DefaultInputItem
              label={''}
              placeholder={''}
              value={account}
              wordLimit={10}
              onChange={(value) => {
                setAccount(value)
              }}
            />
          </div>
          <div className={styles.inputContainer}>
            <DefaultInputItem
              label={''}
              placeholder={''}
              value={name}
              wordLimit={50}
              onChange={(value) => {
                setName(value)
              }}
            />

          </div>
          <div className={styles.inputContainer}>
            <DefaultInputItem
              label={'Email'}
              placeholder={' Email'}
              value={email}
              wordLimit={10}
              onChange={(value) => {
                setEmail(value)
              }}
            />

          </div>
          <div className={styles.inputContainer}>
            <DefaultInputItem
              label={''}
              placeholder={''}
              type={'password'}
              value={password}
              wordLimit={10}
              onChange={(value) => {
                setPassword(value)
              }}
            />

          </div>
          <div className={styles.inputContainer}>
            <DefaultInputItem
              label={''}
              placeholder={''}
              type={'password'}
              value={checkPassword}
              wordLimit={10}
              onChange={(value) => {
                setCheckPassword(value)
              }}
            />

          </div>
          <div className={styles.buttonContainer} onClick={handleClick}>
            <Button type={'fullPill'} value={''} />
          </div>
          <a href="" className={styles.cancel}></a>
        </div>
  )
}

export default SignUpPage
